	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import discussfangwuxinxi from '@/views/discussfangwuxinxi/list'
	import fangwuxinxi from '@/views/fangwuxinxi/list'
	import yonghu from '@/views/yonghu/list'
	import fangwuweixiu from '@/views/fangwuweixiu/list'
	import fangwuzulin from '@/views/fangwuzulin/list'
	import yuyuexinxi from '@/views/yuyuexinxi/list'
	import fangdong from '@/views/fangdong/list'
	import storeup from '@/views/storeup/list'
	import config from '@/views/config/list'
	import users from '@/views/users/list'
	import shenqingtuizu from '@/views/shenqingtuizu/list'
	import fangdongRegister from '@/views/fangdong/register'
	import fangdongCenter from '@/views/fangdong/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/fangdongCenter',
			name: '房东个人中心',
			component: fangdongCenter
		}
		,{
			path: '/news',
			name: '公告资讯',
			component: news
		}
		,{
			path: '/discussfangwuxinxi',
			name: '房屋信息评论',
			component: discussfangwuxinxi
		}
		,{
			path: '/fangwuxinxi',
			name: '房屋信息',
			component: fangwuxinxi
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/fangwuweixiu',
			name: '房屋维修',
			component: fangwuweixiu
		}
		,{
			path: '/fangwuzulin',
			name: '房屋租赁',
			component: fangwuzulin
		}
		,{
			path: '/yuyuexinxi',
			name: '预约信息',
			component: yuyuexinxi
		}
		,{
			path: '/fangdong',
			name: '房东',
			component: fangdong
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/shenqingtuizu',
			name: '申请退租',
			component: shenqingtuizu
		}
		]
	},
	{
		path: '/fangdongRegister',
		name: '房东注册',
		component: fangdongRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
